@import '../../assets/font';
@import '../../assets/animation';

@keyframes animation {
  from {
    top: 100px;
    opacity: 0; }
  to {
    top: 0;
    opacity: 1; } }

.animation {
  animation: animation 0.2s linear; }

.card {
  display: none;
  box-sizing: border-box;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  -webkit-box-shadow: 0 0 5px #000000;
  box-shadow: 0 0 5px #000000;
  position: relative;
  &_active {
    display: block; }

  &:hover  .card__img {
    transform: scale(1.2); }
  &:hover  .card__title {
    color: #3333ff; } }

.card__img-container {
  overflow: hidden;
  font-size: 0; }

.card__img {
  width: 100%;
  object-fit: cover;
  cursor: pointer;
  transition: .5s; }

.card__title {
  padding: 0 10px;
  @include text; }
