@keyframes opacity {
    from {
        opacity: 0; }
    to {
        opacity: 1; } }

@keyframes departure-left {
    from {
        left: -5%; }
    to {
        left: 0; } }

@keyframes departure-bottom {
    from {
        top: 100px; }
    to {
        top: 0; } }

.emergence-left {
    animation: opacity 0.5s linear, departure-left 1s linear;
    animation-fill-mode: forwards;
    animation-delay: 1s;
    opacity: 0; }

.emergence-bottom {
    animation: opacity 0.2s linear, departure-bottom 0.2s linear;
    animation-fill-mode: forwards; }
